import React, { useEffect, useState } from "react"
import PropTypes from "prop-types"
import Typing from 'react-typing-animation';
import './layout.scss';
import { Link } from "gatsby"


const NavItem = (props) => {
  const onMouseOver = (e) => {
    const selector = e.target.parentElement.querySelector('span.selector')
    if (selector) {
        selector.className = 'arrow selector';
    }
  }
  const onMouseOut = (e) => {
    const selector = e.target.parentElement.querySelector('span.selector')
    if (selector) {
        selector.className = 'bullet selector';
    }
  }

  return (
    <div onClick={props.onClick} onMouseOver={onMouseOver} onMouseOut={onMouseOut}><span className="bullet selector"></span>{props.children}</div>
  )
} 

const Checkbox = (props) => {
  return (
    <label>
      <input type="checkbox" className="nes-checkbox" {...props}/>
      <span>{props.label}</span>
    </label>
  );
};

const Layout = ({ children }) => {
  const [animate, toggleAnimate] = useState(true);
  
  useEffect(() => {
    if (animate) {
      document.body.style.animationPlayState = "running";
    } else {
      document.body.style.animationPlayState = "paused";
    }
  }, [animate])

  return (
    <div className="root">
      <div className="nes-container is-rounded is-dark nav">
        <NavItem><Link to="/">Option 1</Link></NavItem>
        <NavItem><Link to="/page-2">Option 2</Link></NavItem>
      </div>
      <div className="check"><Checkbox label="Animate Background?" checked={animate} onChange={() => toggleAnimate(!animate)} /></div>
      <div id="main-content" className="main-content nes-balloon from-left is-dark">
        {children}
      </div>
    </div>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
